body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.search-box {
  position: relative;
  height: 350px;
  margin: 10px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  text-align: left;
  font-size: 10pt;
  font-weight: 500;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: 4px 2px;
}

#timedate {
  position: absolute;
  top: 15px;
  left: 20px;
  font-size: 12pt;
  text-shadow: 1px 1px;
}

#timecat {
  position: absolute;
  top: 15px;
  left: 250px;
  font-size: 10pt;
  text-shadow: 1px 1px;
}

label {
  font-size: 10pt;
}

#position-lat-label {
  position:absolute;
  top: 35px;
  left: 30px;
}

#position-lon-label {
  position:absolute;
  top: 35px;
  left: 140px;
}

#position-lat, #position-lon {
  position: absolute;
  top: 50px;
  height: 21px;
  width: 100px;
  padding: 0px 0 0 10px;
  border: 1px solid black;
  border-radius: 2px;
  box-shadow: 4px 2px gray;
}

#position-lat {
  left: 30px;
}

#position-lon {
  left: 140px;
}

.button-random {
  position: absolute;
  top: 34px;
  left: 260px;
  height: 35px;
  width: 100px;
  margin: 10px 0 0 5px;
  padding: 0px 0px 0px 0px;
  font-weight: 600;
  color: white;
  background-color: teal;
  border: 1px solid magenta;
  border-radius: 15px;
  box-shadow: 2px 2px lightgray;
}
.button-random:hover {
    margin: 8px 0 0 0px;
    width: 110px;
    height: 40px;
}

#minDistance {
  position: absolute;
  top : 75px;
  left: 25px;
  height: 20px;
  text-shadow: 1px 1px;
}

#start-station-id,
#start-station,
#end-station-id,
#end-station {
  position: absolute;
  padding: 0px 0 0 5px;
  border: 1px solid black;
  border-radius: 2px;
  box-shadow: 4px 2px lightgray;
}
#start-station-id {
  top: 115px;
  left: 30px;
  width: 40px;
  height: 30px;
  z-index:5;
}

#start-station {
  top: 115px;
  left: 70px;
  width: 305px;
  height: 30px;
  z-index:2;
}

#start-station-id-label {
  position:absolute;
  top: 100px;
  left: 30px;
}

#end-station-id {
  top: 165px;
  left: 30px;
  width: 40px;
  height: 30px;
  z-index: 50;
}

#end-station {
  top: 165px;
  left: 70px;
  width: 305px;
  height: 30px;
  z-index:2;
}

#end-station-id-label {
  position:absolute;
  top: 150px;
  left: 30px;
}

input {
  font-size: 10pt;
}
#radio-box-time,
#radio-box-profile,
#radio-box-location {
  position: absolute;
  width: 110px;
  height: 100px;
  font-size: 10pt;
  padding: 0 0 0 5px;
  border: 1px solid black;
}
#radio-box-location {
  top: 205px;
  left: 30px;
}
#radio-box-time {
  top: 205px;
  left: 140px;
}
#radio-box-profile {
  top: 205px;
  left:250px;
}


#map {
  height: 700px;
  width: 100%;
  margin: 10px 0px 0px 0px;
  text-align: center;
  border: 1 px solid black;
  border-radius: 5px;
  box-shadow: 2px 4px;
}
/* Always set the map height explicitly to define the size of the div
  * element that contains the map. */



.chart-summary {
  height: 50px;
  text-align: center;
  margin: auto;
  margin-top: 25px;
  font-size: 24px;
}

.chart-detail {
  text-align: center;
  margin: auto;
  font-size: 16px;
}

/* .AppBar {
  text-align: center;
} */

.AppBar-header {
  position: relative;
  background-image: url("https://media.giphy.com/media/o0mdBMezyYk5q/giphy.gif");
  background-color: #222;
  height: 145px;
  padding: 20px;
  color: white;
  border-radius: 20px;
}

#nameBox {
    position: absolute;
    top: 0px;
    left: 50px;
    width: 250px;
    height: 100px;
    padding: 15px;
    margin: 20px 0 0 0;
    text-align: center;
    background: yellow;
    color: black;
    border-radius: 100px;
    border: solid 1px black;
}

.smallprint {
  color: yellow;
  font-size: 10px;
}

.AppBar-title {
  position: absolute;
  top: 50px;
  left: 50%;
  color: white;
  font-size: 4rem;
}

.info-card {
  height: 320px;
  width: 105%;
  margin: 10px 0 0 1px;
  border-radius: 5px;
  border: solid 1px black;
  box-shadow: 2px 4px lightgray;
}

